import { mapState, mapMutations } from 'vuex'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  computed: {
    ...mapState([
      'checkStatus'
    ])
  },
  methods: {
    ...mapMutations({
      setCheckStatus: 'SET_CHECK_STATUS'
    }),
    // 关闭弹窗
    onShowStatusFn () {
      this.$emit('popupStatusChild', false)
    },
    // 复选框勾选
    onCheckStatusFn (state) {
      this.setCheckStatus(state)
    }
  },
  props: {
    showState: {
      type: Boolean,
      default: false
    },
    obj: {
      type: String,
      default: ''
    },
    oRule: {
      type: String,
      default: ''
    }
  }
}
